import React, { useEffect, useState } from "react";
import { Box, Typography } from "@mui/material";
import { useTheme } from "@mui/styles";
import { ThemeOptions } from "@mui/system";
import { useNavigate } from "react-router-dom";
import { ButtonKit } from "../../../components/kit/Button";
import { KaranbalaLogoTextSvg } from "../../../assets";
import { SelectKit } from "../../../components/kit/Select";

const SubjectiveTest = () => {
    const theme: ThemeOptions = useTheme();
    const navigate = useNavigate();
    const [selectValue, setSelectValue] = useState(2);
    useEffect(() => {
        window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
    }, []);
    const option = [
        { title: " ", value: 0 },
        { title: " ", value: 1 },
        { title: " ", value: 2 },
        { title: " ", value: 3 },
        { title: " ", value: 4 },
        { title: " ", value: 5 },
    ];
    return (
        <Box margin={"0.75rem 3.25rem 0 3.25rem"} paddingBottom={"7.5rem"}>
            <Box display={"flex"} justifyContent={"end"}>
                <ButtonKit onClick={() => navigate("/")}>
                    {" "}
                    <KaranbalaLogoTextSvg />
                </ButtonKit>
            </Box>
            <Box margin={"3.19rem 0 0 0"}>
                <SelectKit
                    options={option}
                    label={""}
                    defaultValue={selectValue}
                    onChange={({ target: { value } }) => {
                        const newValue: number = value as number;

                        setSelectValue(newValue);
                    }}
                ></SelectKit>
            </Box>
            <Box
                margin={"2rem 0"}
                display={"flex"}
                flexWrap={"wrap"}
                justifyContent={"space-between"}
            >
                <Box flexGrow={1} margin={"2rem 0"}>
                    <Box>
                        <ButtonKit fullWidth variant="contained">
                            <Typography> </Typography>
                        </ButtonKit>
                    </Box>
                    <Box
                        sx={{ "& button": { flexGrow: 1 } }}
                        marginTop={"2rem"}
                        display={"flex"}
                        gap={"0.5rem"}
                    >
                        <ButtonKit variant="contained">
                            <Typography>1</Typography>
                        </ButtonKit>
                        <ButtonKit variant="contained">
                            <Typography>2</Typography>
                        </ButtonKit>
                        <ButtonKit variant="contained">
                            <Typography>3</Typography>
                        </ButtonKit>
                        <ButtonKit variant="contained">
                            <Typography>4</Typography>
                        </ButtonKit>
                        <ButtonKit variant="contained">
                            <Typography>5</Typography>
                        </ButtonKit>
                        <ButtonKit variant="contained">
                            <Typography>6</Typography>
                        </ButtonKit>
                    </Box>
                </Box>
                <Box
                    justifyContent={"center"}
                    alignItems={"center"}
                    textAlign={"center"}
                    display={"flex"}
                    flexWrap={"wrap"}
                    flexGrow={1}
                    margin={"2rem 0"}
                >
                    <Typography fontSize={"3.6rem"} variant="subtitle1">
                          
                    </Typography>
                </Box>
                <Box margin={"2rem 0"} textAlign={"right"} flexGrow={1} flexWrap={"wrap"}>
                    <Box>
                        <ButtonKit fullWidth variant="contained">
                            <Typography> </Typography>
                        </ButtonKit>
                    </Box>
                    <Box
                        sx={{ "& button": { flexGrow: 1 } }}
                        marginTop={"2rem"}
                        display={"flex"}
                        gap={"0.5rem"}
                        justifyContent={"end"}
                    >
                        <ButtonKit variant="contained">
                            <Typography>1</Typography>
                        </ButtonKit>
                        <ButtonKit variant="contained">
                            <Typography>2</Typography>
                        </ButtonKit>
                        <ButtonKit variant="contained">
                            <Typography>3</Typography>
                        </ButtonKit>
                        <ButtonKit variant="contained">
                            <Typography>4</Typography>
                        </ButtonKit>
                        <ButtonKit variant="contained">
                            <Typography>5</Typography>
                        </ButtonKit>
                        <ButtonKit variant="contained">
                            <Typography>6</Typography>
                        </ButtonKit>
                    </Box>
                </Box>
            </Box>
            <Box
                bgcolor={theme?.palette?.grey[100]}
                display={"flex"}
                justifyContent={"space-between"}
                borderRadius={"1rem"}
                padding={"2rem"}
            >
                <Box display={"flex"}>
                    <Typography> : </Typography>
                    <Typography></Typography>
                </Box>
                <Box display={"flex"}>
                    <Typography> : </Typography>
                    <Typography> </Typography>
                </Box>
                <Box display={"flex"}>
                    <Typography>   : </Typography>
                    <Typography>    -  :</Typography>
                </Box>{" "}
            </Box>

            <Box
                display={"flex"}
                justifyContent={"space-between"}
                sx={{ "& div": { flexBasis: "30%", padding: "2rem 0" } }}
                gap={"2rem"}
                margin={"2rem 0"}
            >
                <Box
                    display={"flex"}
                    justifyContent={"center"}
                    borderRadius={"1rem"}
                    bgcolor={theme?.palette?.grey[100]}
                >
                    <Typography variant="subtitle1">  </Typography>
                </Box>
                <Box
                    display={"flex"}
                    justifyContent={"center"}
                    borderRadius={"1rem"}
                    bgcolor={theme?.palette?.grey[100]}
                >
                    <Typography variant="subtitle1"> </Typography>
                </Box>
                <Box
                    display={"flex"}
                    justifyContent={"center"}
                    borderRadius={"1rem"}
                    bgcolor={theme?.palette?.grey[100]}
                >
                    <Typography variant="subtitle1">-- </Typography>
                </Box>
            </Box>
            <Box borderRadius={"1rem"} padding={"2rem"} margin={"2rem 0"}>
                <Box display={"flex"} justifyContent={"space-between"}>
                    <Typography fontSize={"3.2rem"} variant="subtitle1">
                        
                    </Typography>
                    <Typography fontSize={"1.8rem"} variant="caption">
                         : :
                    </Typography>
                </Box>
                <Box borderRadius={"1rem"} padding={"2rem"} margin={"2rem 0 0 0"}>
                    <Typography>-    </Typography>
                </Box>

                <Box margin={"3rem 0"} display={"flex"} gap={"1rem"} justifyContent={"center"}>
                    <ButtonKit variant="contained">
                        <Typography> </Typography>
                    </ButtonKit>
                    <ButtonKit variant="outlined">
                        <Typography> </Typography>
                    </ButtonKit>
                    <ButtonKit variant="outlined">
                        <Typography> </Typography>
                    </ButtonKit>
                </Box>
                <Box
                    display={"flex"}
                    justifyContent={"space-between"}
                    sx={{ "& div": { flexBasis: "30%", padding: "2rem 0" } }}
                    gap={"2rem"}
                    margin={"2rem 0"}
                >
                    <Box
                        display={"flex"}
                        justifyContent={"center"}
                        borderRadius={"1rem"}
                        bgcolor={theme?.palette?.grey[100]}
                        flexGrow={"1"}
                    >
                        <Typography variant="subtitle1">  ( )</Typography>
                    </Box>
                    <Box
                        display={"flex"}
                        justifyContent={"center"}
                        borderRadius={"1rem"}
                        bgcolor={theme?.palette?.grey[100]}
                        flexGrow={"1"}
                    >
                        <Typography variant="subtitle1">  ( )</Typography>
                    </Box>
                </Box>
            </Box>
        </Box>
    );
};

export default SubjectiveTest;
